import React, { Component } from 'react'

export default class FormDemo3 extends Component {
    state = {
        name: 'sridhar',
        password:'password'
    }
    changHand = (e) => {
        this.setState(this.state.name = [...e.target.value])
    }
    onSub = (e)=>{
        e.preventDefault();
        console.log(this.state.name)
    }
    render() {
        return <>
            <h4>from demo3</h4>
            <form onSubmit={this.onSub}>
                <input name='name' onChange={this.changHand} /><br/>
                <input name='password' onChange={this.changHand} /><br/>
                <input type='submit' value='login'/>
                <p>{this.state.name}</p>
            </form>
        </>
    }
}
