import React from 'react'


export default function Pagenation2({ data, pageHandler }) {
    let pagButtuns = []
    for (let i = 1; i < (data.length / 10) + 1; i++) {
        pagButtuns.push(i)
    }

    return <div className='text-center my-5'>
        {
            pagButtuns.map((page, ind) => {
                return <><button key={ind} className='btn btn-info mx-2'
                    onClick={() => { pageHandler(page) }}>{page}</button>
                </>
            })
        }
    </div>
}
