import React, { useState } from 'react'
// import axios from 'axios';
import eproductsdata from './eprodcutsdata'
import './eproduct.css'
import Pagenation2 from './productscard/assignment/pagenation/Pagenation2';
import Search from './productscard/assignment/search/Search';
import ReactPaginate from 'react-paginate';
import { Link } from 'react-router-dom';

export default function Eproducts() {

  const [data, setData] = useState(eproductsdata)
  const [pagePer, setpagePer] = useState(data.slice(0, 10))

  const pageHandler = (PageNumber) => {
    setpagePer(data.slice((10 * PageNumber) - 10, 10 * PageNumber));
  }
  // console.log(data);
  return (
    <>
      {/* <Search data={data} /> */}
      {
        data.length > 1 ? <div className="container bg-trasparent  p-3">
          <div className="row row-cols-1 row-cols-xs-2 row-cols-sm-2 row-cols-lg-4 g-3">
            {

              pagePer.map((a, ind) => {

                // props de_stracturing 
                const { image, price, rating, description, id } = a;

                return (<div className="col-lg-4 col-md-6 d-flex " key={ind}>
                  <div className="card h-100 shadow-sm">
                    <img src={image} className="card-img-top" alt="..." />
                    <div className="card-body">
                      <div className="clearfix mb-3">
                        <span className="float-start badge rounded-pill bg-danger"> {price}/-</span> <span className="float-end bg-warning rounded-pill badge">{rating.rate} <i className="fa fa-star" aria-hidden="true"></i></span>
                      </div>
                      <h5 className="card-title">
                        {description}
                      </h5>
                      <div className="d-grid gap-2 my-4">
                        <Link  className="btn btn-dark" to={`/ProductDetails/${id}`}>View Details</Link>
                      </div>
                    </div>
                  </div>
                </div>)

              })
            }
          </div>
          <Pagenation2 data={data} pageHandler={pageHandler} />

        </div>
          :
          <h1>data not found</h1>
      }


    </>
  )
}

