import React, { Component } from 'react'

export default class LifeCycledemo2 extends Component {
    constructor(props) {
        super(props)
        this.state = {
            users: [],
        }
        console.log('constructor...1');
        console.log(this.state.users);
    }
    getUser = async () => {
        const user = await fetch("https://jsonplaceholder.typicode.com/users");
        const loadData = await user.json();
        console.log(loadData) //data showing in console
        this.setState({ users: [...loadData] })
        
    }


    render() {
        console.log('render...3')
        // console.log(this.state.users);
        return <>
            <table className='table table-striped text-center table-bordered table-dark'>
                <thead>
                    <tr>
                        <th>Name</th>
                        <th>UserName</th>
                        <th>Phone Number</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        this.state.users.map((e, ind) => {
                            return <tr key={ind}>
                                <td >{e.name}</td>
                                <td >{e.username}</td>
                                <td >{e.phone}</td>
                            </tr>
                        })
                    }

                </tbody>
            </table>
        </>
    }
    componentDidMount() {
        console.log('componentDidMount...4')
        this.getUser();
    }

}
